import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const types = {
  SET_IS_MEMBEREMAIL: 'SET_IS_MEMBEREMAIL', // 是否是管理员邮箱
}
const state = { // 需要维护的状态
  memberEmail: '',
  content:'',
  messageList:[],
  titles:[]
}
const getters = {
  memberEmail: state => state.memberEmail,
}
const mutations = {
  [types.SET_IS_MEMBEREMAIL](state, memberEmail) {
    state.memberEmail = memberEmail
  },
}
const actions = {
  set_is_memberemail: ({ commit }, memberEmail) => {
    commit(types.SET_IS_MEMBEREMAIL, memberEmail)
  },
}
export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})