<template>
  <div id="introduce" class="home">
    <nav
      class="navbar navbar-expand-md navbar-light bg-light border-bottom fixed-top"
      id="myNavbar"
    >
      <div class="container">
        <a class="navbar-brand" href="#">个人首页</a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#myNavbarContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="myNavbarContent">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <a class="nav-link active" href="#home">基本介绍</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#feature">日常学习</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#product">我的笔记</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#hobby">兴趣爱好</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#message">留言板</a>
            </li>
            <li class="nav-item" v-if="loginKey.length == 0">
              <a class="nav-link" href="#"><span @click="login()">登录</span></a>
            </li>
            <li class="nav-item" href="#" v-if="loginKey.length != 0">
              <a class="nav-link"><span @click="loginout()">退出</span></a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <section id="home" class="py-5 text-center" style="margin-top:16px;">
      <div class="container">
        <img src="./img/introduce1.png" alt class="w-100" />
        <p
          class="self-say"
        >我是一个小小的程序员，喜欢编程，喜欢全力以赴做好每一件事情，喜欢修复bug后的欣慰，喜欢项目上线时的成就感，喜欢做出的网站有人欣赏，喜欢不断学习新的技能，喜欢不断充实自己，虽然没有时间打扮，发际线也在悄悄后移，但既然选择了这条路，便要以梦为马，不负韶华……</p>
      </div>
    </section>
    <section id="feature" class="pt-5">
      <h3 class="text-center mb-3 day-study">日常学习</h3>
      <div class="bg-light">
        <div class="container py-5">
          <div class="row flex-row-reverse">
            <div class="col-md-6 mb-3 mb-md-0 wow fadeInRight">
              <img src="./img/book.jpg" alt class="w-100" />
            </div>
            <div class="col-md-6 wow fadeInLeft">
              <p class="self-say">
                每个人都需懂一门技术作为安身立命之本，那么该方向的专业技能就需不断学习，无论是经典原著，还是最新趋势，都得长期跟踪学习。我喜欢翻阅一些较为基础但也很实用的编程书籍，如JavaScript Dom 编程艺术，编程小白的第一本Python入门书，Python从入门到精通等；喜欢时常看一些技术网址，如GitHub，CSDN，腾讯课堂，妙味视频等；也会让自己遇见更优秀的编程者，和他们一起学习一起追寻……这些都可以让自己在编程这一专业领域中有所进步。下面是我的博客，会分享一些自己在编程中遇到的问题及小实例，感兴趣的可以看一下
                <a
                  href="https://blog.csdn.net/qq_30834535"
                  style="color:#71aefd"
                >https://blog.csdn.net/qq_30834535</a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section id="product" class="mt-5 pt-5">
      <h3 class="text-center mb-3 day-study">我的笔记</h3>
      <div class="container">
        <div class="row wow fadeIn" style="justify-content: center;">
          <div class="col-md-3 mb-3 news" v-for="(elem,index) in titles" :key="index" @click="seeNoteContent(elem,index)">
            <div class="new-son">
              <p class='new-son-son'>{{elem.title}}</p>
            </div>
          </div>
          <div class="col-md-3 mb-3 news">
            <div style="width:100%;" @click="addNote()">
              <img src="./img/add.png" style="width:40px;height:40px;" alt="">
            </div>
          </div>
        </div>
      </div>
    </section>
    <section id="hobby" class="pt-5">
      <h3 class="text-center mb-3 day-study">兴趣爱好</h3>
      <div class="bg-light">
        <div class="container py-5">
          <div class="row flex-row-reverse">
            <div class="col-md-6 mb-3 mb-md-0 wow fadeInRight">
              <img src="./img/run.png" alt class="w-100" />
            </div>
            <div class="col-md-6 wow fadeInLeft">
              <p
                class="self-say"
              >很多人觉得静下来最好的方式，是给自己一个房间，没有手机，没有电脑，安安静静的坐着或者躺着思考人生。但是这样是静下来，更多的是一种束缚和孤独。跑步中我们虽然在运动，但是脑子想的东西并不多，反而提供了一种静下来的空间。所以我喜欢跑步，当有事情想不通时或者压力过大时，不妨去跑跑步，在这个过程中，让大脑去重新思考，也许会有更好的答案。同时跑步还能增强身体体能,但是不要过量哦~~~</p>
            </div>
          </div>
        </div>
        <div class="container py-5" style="border-top: 1px solid #e6e6e6">
          <div class="row flex-row-reverse">
            <div class="col-md-6 wow fadeInRight">
              <p class="self-say">我还是一个名副其实的"铲屎官"，喜欢小动物,尤其小狗，猫咪，但是由于在外工作的一些原因，无法再养一条小狗狗,有点可惜!</p>
              <p
                class="self-say"
              >左图是我养的一只大胖橘，很可爱吧，O(∩_∩)O哈哈~。传说"十个橘猫九个胖，还有一个非常胖"。但是现在我还没有这个体验，可能还没有到非常胖的时候吧</p>
            </div>
            <div class="col-md-6 mb-3 mb-md-0 wow fadeInLeft">
              <img src="./img/Kitty.png" alt class="w-100" style="height:260px" />
            </div>
          </div>
        </div>
        <div class="container py-5" style="border-top: 1px solid #e6e6e6">
          <div class="row flex-row-reverse">
            <div class="col-md-6 mb-3 mb-md-0 wow fadeInRight">
              <img src="./img/games.jpg" alt class="w-100" style="height:310px" />
            </div>
            <div class="col-md-6 wow fadeInLeft">
              <p
                class="self-say"
              >我是一名"游戏爱好者"，从小到大一直是父母眼中的"好孩子"，老师眼中的"乖学生"，受到的劝告也是不要沉浸于游戏，这样百害而无一利，容易荒废学业等等。在大学期间抱着好奇的心态接触了英雄联盟这个游戏，发现很有意思，锻炼大脑的反应速度，开团的时机，团队的协作能力，还可以快速融入到集体，反正感觉很棒。其实这就是一个兴趣爱好，只要不过度沉迷，都是可以接受的，做任何事情都有利有弊，关键还看自己是否能使用得当吧。之后还接触了王者，吃鸡等手游，对游戏这个行业有很大的憧憬，希望自己有机会可以转到游戏行业，不光做一个游戏爱好者还能做一个游戏开发者</p>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section id="message" class="pt-5">
      <h3 class="text-center mb-3 day-study">留言板</h3>
      <div class="bg-light">
        <div class="container py-5">
          <div class="row flex-row-reverse">
            <div class="col-md-6 mb-3 mb-md-0 wow fadeInRight">
              <div class= "w-100" style="border:1px solid #bfbfbf;height:250px;background:#ffffff;overflow:auto;">
                <div style="width:100%; position:relative" v-for="(elem,index) in messageList">
                  <img style="width:25px;height:25px;" src="./img/portrait.png" alt="">
                  <span style="position:absolute;top:6px;left:28px;font-size:14px;">{{elem.username}}</span>
                  <p style="wdith:90%;padding:8px 5%;">{{elem.content}}</p>
                </div>
              </div>
            </div>
            <div class="col-md-6 wow fadeInLeft">
              <div class="w-100" style="position:relative">
                <span style="position:absolute;font-size:14px;top:11px;left:2px;color:#8b8b8b;">昵 称:</span>
                <input class="w-100" style="height:34px;padding: 5px 0 5px 46px;" type="text" placeholder="请输入您的用户名~~~" v-model="name">
              </div>
              <div class="w-100" style="margin-top:14px;">
                <textarea class="w-100" style="padding:5px;" placeholder="留言" v-model="message" cols="30" rows="10"></textarea>
              </div>
              <div class="w-100" style="margin-top:14px;">
                <button class="w-100" style="height:34px;line-height:34px;background:#1296db;color:#ffffff;outline:none;border: 1px solid transparent;" @click="sendMessage()">发送</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import login from './login'
import {getCookie,delCookie} from "../assets/js/cookie.js";
export default {
  name: "home",
  data() {
    return {
      titles:[],
      isShowLoginPop: false,
      loginKey:'',
      content:"",
      isShowContent:false,
      name:'',
      message:'',
      messageList:[]
    };
  },
  created() {
    let key = localStorage.getItem('accountInfo')
    this.loginKey = key ? key : '';
    console.log(this.loginKey);
    if(this.$store.state.messageList.length == 0) {
      this.$axios.get('/api/messages/findMessages').then(res => {
        this.$store.state.messageList = this.messageList = res.data
        console.log(res);
      })
    } else {
      this.messageList = this.$store.state.messageList
    }
    if(this.$store.state.titles.length == 0) {
      let promise = new Promise((resolve, reject) => {
        this.$axios.get('/api/notes/findNotes').then(res => {
          this.$store.state.titles = this.titles = res.data;
          resolve(res.data);
          })
      }); 
    } else {
      this.titles = this.$store.state.titles
    }
  },
  methods: {
    sendMessage(){
      let submitInfo = {
        username: this.name,
        content: this.message
      }
      this.$axios.post(this.GLOBAL.host+"/api/messages/addMessage", submitInfo).then(res => {
        if(res.data.success) {
          this.messageList.unshift(submitInfo)
          this.$message("留言成功~~~~");
          this.name = '';
          this.message = ''
        } else {
          this.$message("留言失败,请重新留言~~~~");
        }
      });
    },
    seeNoteContent(elem,index){
      this.$router.push('/noteDisplay');
      this.$store.content = elem;
    },
    addNote(){
      let isManager = this.loginKey.split('&')
      if(this.loginKey.length !=0 && isManager[0] == '1937581264@qq.com') {
        this.$router.push('/writeNote')
      } else {
        this.$message({
          message: '您不是管理员,不能添加笔记哦~~~',
          duration: '2000'
        });
      }
      
    },
    login(){
      this.$router.push('/login')
    },
    loginout() {
      localStorage.setItem('accountInfo', '')
      if(!localStorage.getItem('accountInfo')) {
        this.$message('退出成功');
        this.loginKey = '';
      }
    }
  },
  mounted() {
    $(function() {
      var myNabvarHt = $("#myNavbar").outerHeight();
      var myNavbarContent = $("#myNavbarContent");
      $("#myNavbarContent a.nav-link").click(function(e) {
        e.preventDefault();
        myNavbarContent.collapse("hide");
        if($(this.hash).offset()) {
          $("html,body").animate({
            scrollTop: $(this.hash).offset().top - myNabvarHt
          });
        }
      });
      new WOW().init();
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "../assets/css/introduce.css";
</style>
