<template>
  <section id="loginComponent">
    <div class="mask">
      <div class="login-pop">
        <div class="login-title">
          <p @click="returnPage()">
            <img src="./img/return.svg" />
            <span>返回首页</span>
          </p>
          <p @click="register()">{{doc.docPage}}</p>
        </div>
        <slot></slot>
        <button @click="finish()">{{doc.docBtn}}</button>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  props:{
    doc:{
      type:Object,
    }
  },
  data(){
    return {
    }
  },
  methods: {
    finish(){
      this.$emit('finish')
    },
    returnPage() {
      this.$router.push('/introduce')
    },
    register() {
      this.doc.key == 'login' ? this.$router.push('/register') : this.$router.push('/login')
    }
  }
};
</script>
<style lang="stylus" scoped>
@media screen and (min-width: 700px)
  #loginComponent {
    width: 100%;
    height: 100%;
    background: url('./img/login_bg.png') no-repeat;
    .mask {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      .login-pop {
        width 380px
        padding:40px 20px;
        background: rgba(225, 225, 225, 0.3);
        border-radius 20px;
        position fixed;
        right 12%;
        top: 26%;
        .login-title {
          display: flex;
          justify-content: space-between;
          img {
            width: 20px;
            height: 20px;
            position absolute
            left 0;
            top 5px
          }
          p {
            color: #ffffff;
            line-height: 30px;
            font-size: 16px;
            position relative;
            cursor: pointer;
            span{
              margin-left 20px;
            }
          }
          p:nth-of-type(1) {
            cursor: pointer;
          }
        }
        button {
            margin-top: 36px;
            width: 100%;
            height: 56px;
            line-height 56px;
            background: #395eff;
            color: #ffffff;
            font-size: 22px;
            border-radius: 6px;
            cursor: pointer;
            border: 1px solid transparent;  //自定义边框
            outline: none;    //消除默认点击蓝色边框效果
          }
      }
    }
  }
@media screen and (max-width: 700px)
  #loginComponent {
    width: 100%;
    height: 100%;
    background: url('./img/login_bg.png') no-repeat;
    .mask {
      width: 100%;
