        position fixed;
        left 50%;
        top: 20%;
        margin-left: -150px;
        .login-title {
          display: flex;
          justify-content: space-between;
          img {
            width: 20px;
            height: 20px;
            position absolute
            left 0;
            top 5px
          }
          p {
            color: #ffffff;
