<template>
  <section id="writeNote">
    <div class="title">
      <input type="text" v-model="title" placeholder="请输入标题~~~" />
    </div>
    <div class="content-info">
      <quill-editor
        v-model="content"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)"
      ></quill-editor>
    </div>
    <button @click="submit()">提交</button>
  </section>
</template>
<script>
import { quillEditor } from "vue-quill-editor";
import * as Quill from 'quill'
// import ImageResize from 'quill-image-resize-module'
// Quill.register('modules/imageResize', ImageResize)
export default {
  data() {
    return {
      title: "",
      content: "",
      editorOption: {}
    };
  },
  created() {},
  methods: {
    onEditorFocus() {
      console.log("focus");
    },
    onEditorBlur() {
      console.log("blur");
    },
    onEditorChange() {
      console.log("change");
    },
    submit() {
      let submitInfo = {
        title: this.title,
        content: this.content,
      }
      this.$axios.post("/api/notes/addNotes", this.$qs.stringify(submitInfo)).then(res => {
        if(res.data.success) {
          this.$router.push("/introduce");
        }
      });
    }
  }
};
</script>
<style lang="stylus" scoped>
#writeNote {
  .title {
    width: 100vw;
    height: 5vh;

    input {
      width: 100vw;
      height: 5vh;
      border: 1px solid #8b8b8b;
    }
  }

  .content-info {
    margin-top: 2vh;
    width: 100vw;
    height: 80vh;

    /deep/ .quill-editor {
      .ql-toolbar.ql-snow + .ql-container.ql-snow {
        width: 100vw;
        height: 80vh;
        overflow-y: auto;
      }
    }
  }

  button {
    width: 46vw;
    height: 4vh;
    line-height: 4vh;
    background: #1296db;
    color: #ffffff;
    font-size: 0.8vw;
    border-radius: 4px;
    margin: 4vh 0 0 27vw;
  }
}
</style>
