<template>
  <section id="login">
    <loginComponent :doc='doc' @finish='finish'>
      <div class="login-content">
        <div class="card1">
          <span>账&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号:</span>
          <input type="text" v-model="email" placeholder="请输入邮箱账号" />
        </div>
        <div class="card1">
          <span>密&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码:</span>
          <input type="password" v-model="psd" placeholder="请输入密码" />
        </div>
        <div class="card1">
          <span>确认密码:</span>
          <input type="password" v-model="psdAgain" placeholder="请再次输入密码" />
        </div>
      </div>
    </loginComponent>
  </section>
</template>
<script>
import loginComponent from './loginComponent'
import { Message } from 'element-ui';
import { setTimeout } from 'timers';
export default {
  components:{
    loginComponent
  },
  data(){
    return {
      doc:{
        docBtn: '立即注册',
        docPage: '登录',
        key: 'register'
      },
      email:'',
      psd:'',
      psdAgain:''
    }
  },
  methods: {
    finish(){
      let reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if(this.email=='' || this.psd =='' || this.psdAgain == '') {
        this.messageWarn('请确保邮箱及密码不为空~~~');
      } else if(!reg.test(this.email)) {
        this.messageWarn('请确保正确的邮箱书写格式~~~');
      } else if(this.psd.length < 6) {
        console.log(this.psd.length);
    
        this.messageWarn('密码的长度至少6位哦~~~');
      } else if(this.psd !== this.psdAgain) {
        this.messageWarn('请保证两次输入密码相同~~~');
      } else {
        this.toLogin();
      }
    },
    toLogin(){
      let submitInfo = {
          email: this.email,
          password: this.psd,};
        let promise = new Promise((resolve,reject) =>{
          this.$axios.post('/api/users/register',this.$qs.stringify(submitInfo)).then(res => {
            console.log(res);
            this.messageWarn('注册成功,请重新登陆~~~');
            resolve(res.data);
            }).catch(err => {
              console.log(err);
              reject(err);
          });
        })
        promise.then((val)=>{
          setTimeout(()=>{
            this.$router.push('/login')
          },2000)
        }).catch((err) => {
          console.log(err);
          this.messageWarn('注册失败啦，请重新注册~~~');
        });
    },
    messageWarn(val){
      this.$message({
        message: val,
        center: true
      });
    }
  }
};
</script>
<style lang="stylus" scoped>
#login {
  width: 100%;
  height: 100%;
  background: url('./img/login_bg.png') no-repeat;
  .login-footer {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    p {
      color: #ffffff;
      line-height: 30px;
      font-size: 16px;
      cursor: pointer;
      position relative
      input {
        position absolute;
        left 0;
        top 6px;
        width 20px;
        height 20px;
      }
      span{
        margin-left 24px
      }
    }
  }
  .login-content {
    width: 100%;
    .card1 {
      width: 100%;
      height: 50px;
      line-height 50px
      position relative
      margin-top:20px;
      span {
        position absolute;
        left 16px
        font-size 22px;
        color #7e7e7e;
      }
      input {
        width: 100%;
        height: 50px;
        padding-left: 116px;
        border-radius: 10px;
        outline none;
        font-size 16px;
        color #7e7e7e;
      }
    }
  }
}
</style>
