<template>
  <section id="login">
    <loginComponent :doc="doc" @finish="finish">
      <div class="login-content">
        <div class="card1">
          <span>账号:</span>
          <input type="text" v-model="email" placeholder="请输入邮箱账号" />
        </div>
        <div class="card1">
          <span>密码:</span>
          <input type="password" name="password" v-model="psd" placeholder="请输入密码" />
        </div>
      </div>
      <div class="login-footer">
        <p>
          <input type="checkbox" v-model="remeberPsd" />
          <span>记住密码</span>
        </p>
        <p></p>
      </div>
    </loginComponent>
  </section>
</template>
<script>
import loginComponent from "./loginComponent";
import {setCookie,getCookie,delCookie} from "../assets/js/cookie.js";
export default {
  components: {
    loginComponent
  },
  data() {
    return {
      doc: {
        docBtn: "立即登录",
        docPage: "注册",
        key: "login"
      },
      email: "",
      psd: "",
      remeberPsd: true,
      loginInfo:''
    };
  },
  created() {
    this.loginInfo = getCookie('accountInfo');
    if(this.loginInfo.length != 0) {
      console.log(this.loginInfo);
      let arr = this.loginInfo.split('&');
      this.email = arr[0];
      this.psd = arr[1];
    } else {
      console.log('1111');
      this.email ='';
      this.psd = '';
    }
  },
  methods: {
    finish() {
      let reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if (this.email == "" || this.psd == "") {
        this.messageWarn("请确保邮箱及密码不为空~~~");
      } else if (!reg.test(this.email)) {
        this.messageWarn("请确保正确的邮箱书写格式~~~");
      } else {      
        this.toLogin();
      }
    },
    toLogin() {
      let submitInfo = {
        email: this.email,
        password: this.psd
      };
      this.$axios.post("/api/users/login", this.$qs.stringify(submitInfo)).then(res => {
          console.log(res.data.email);
          let accountInfo = this.email + "&" + this.psd;
          console.log(accountInfo);
          if(this.remeberPsd) {
            localStorage.setItem('accountInfo',accountInfo)
          } else {
           localStorage.setItem('accountInfo', '')
          }
          setTimeout(() => {
            this.$router.push("/introduce");
          }, 2000);
        })
    },
    messageWarn(val) {
      this.$message({
        message: val,
        center: true
      });
    }
  }
};
</script>
<style lang="stylus" scoped>
#login {
  width: 100%;
  height: 100%;
  background: url('./img/login_bg.png') no-repeat;

  .login-footer {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;

    p {
      color: #ffffff;
      line-height: 30px;
      font-size: 16px;
      cursor: pointer;
      position: relative;

      input {
        position: absolute;
        left: 0;
        top: 6px;
        width: 20px;
        height: 20px;
      }

      span {
        margin-left: 24px;
      }
    }
  }

  .login-content {
    width: 100%;

    .card1 {
      width: 100%;
      height: 50px;
      line-height: 50px;
      position: relative;
      margin-top: 20px;

      span {
        position: absolute;
        left: 16px;
        font-size: 22px;
        color: #7e7e7e;
      }

      input {
        width: 100%;
        height: 50px;
        padding-left: 80px;
        border-radius: 10px;
        outline: none;
        font-size: 16px;
        color: #7e7e7e;
      }
    }
  }
}
</style>
